export default {

    query: {
        status: [
            { id: '0', name: '' },
            { id: '1', name: '' },
            { id: '2', name: '' }
        ],
        userStatus: [
            { id: '0', name: '' },
            { id: '1', name: '' },
            { id: '2', name: '' }
        ]
    },

    use: {
        status: [
            { id: '1', name: '' },
            { id: '2', name: '' }
        ],
        userStatus: [
            { id: '1', name: '' },
            { id: '2', name: '' }
        ],
        modulType: [
            { id: '1', name: '' },
            { id: '2', name: '' }
        ],
        cityArray: [
            { id: '1101', name: '' },
            { id: '4403', name: '' }
        ]
    },
    pageSize: 20,

    resArray: [
        { retcode: '0', status: true, retinfo: '' }
    ],

    paramsHead: {
        msgtype: "request",
        remark: "",
        version: "0.01"
    },

    //
    current_system_id: '1',

    //
    generalStr: (rule, value, callback) => {
        if (value == '') {
            callback(new Error(''));
        }
        callback();
    },

    arrayRemove(array, val) {
        let temArray = [];
        array.forEach((m, index) => {
            if (m != val) {
                temArray.push(m);
            }
        });
        return temArray;
    }
}


